.navbar{
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: center;
    border: 1px solid gray;
}

.nav-left{
    display: flex;
    flex-direction: row;
    width: 30%;
}

.nav-left img{
    height: 60px;
    width: 60px;
    border-radius: 550%;
    margin: 6px;
    padding: 6px;
}

.select-class{
    display: flex;
    align-items: center;
    justify-content: center;

}
.nav-right{
    display: flex;
    width: 70%;
}

.nav-right input{
    height: 30px;
    border-radius: 30px;
    width: 600px;
    padding: 10px;
    border: 1px solid black;
}